<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/goods' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-alert
        title=""
        type="info"
        show-icon
        center
        :closable="false"
      ></el-alert>

      <!--  -->
      <el-steps
        :space="200"
        :active="activeIndex - 0"
        finish-status="success"
        align-center
      >
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <!-- tab -->
      <el-form
        ref="addFormRef"
        :model="addForm"
        :rules="addFormRules"
        label-width="80px"
        label-position="top"
      >
        <el-tabs
          v-model="activeIndex"
          tab-position="left"
          :before-leave="beforeTabLeave"
          @tab-click="tabClicked"
        >
          <!--   -->
          <!--  -->
          <el-tab-pane name="0" label="">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                v-model="addForm.goods_cat"
                :options="cateList"
                :props="cateCascaderProps"
                @change="cateChanged"
                clearable
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane name="1" label="">
            <el-form-item
              v-for="item in manyTableData"
              :label="item.attr_name"
              :key="item.attr_id"
            >
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  :label="attr_val"
                  v-for="(attr_val, i) in item.attr_vals"
                  :key="i"
                  border
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane name="2" label="">
            <el-form-item
              :label="item.attr_name"
              v-for="item in onlyTableData"
              :key="item.attr_id"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane name="3" label="">
            <!--  -->
            <el-upload
              class="upload-demo"
              :action="actionUrl"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headersObj"
              :on-success="picUploaded"
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane name="4" label="">
            <!--  -->
            <quill-editor
              v-model="addForm.goods_introduce"
            />
            <el-button type="primary" class="btnAdd" @click="addGoods"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="previewDialogVisible"
      width="50%"
    >
      <img :src="previewPath" alt="" />
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'

export default {
  data () {
    return {
      activeIndex: '0',
      // 
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        // 
        goods_cat: [],
        // 
        pics: [],
        // 
        goods_introduce: '',
        //  ``  ``
        attrs: []
      },
      // 
      addFormRules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      cateList: [],
      // 
      cateCascaderProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      actionUrl: 'http://www.ysqorz.top:8888/api/private/v1/upload',
      // 
      headersObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 
      previewPath: '',
      // 
      previewDialogVisible: false
    }
  },
  computed: {
    cateId () {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  },
  created () {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('!')
      }
      this.$message.success('!')
      this.cateList = res.data
      // console.log(res.data)
    },
    // 
    cateChanged () {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },
    // 
    beforeTabLeave (activeName, oldActiveName) {
      // ,
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('')
        return false
      }
    },
    // 
    async tabClicked () {
      // tab,,
      if (this.activeIndex === '1') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: {
              sel: 'many'
            }
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('!')
        }
        // 
        res.data.forEach((item) => {
          item.attr_vals =
            item.attr_vals.trim().length === 0 ? [] : item.attr_vals.split(',')
        })
        // console.log(res.data)
        this.manyTableData = res.data
        this.$message.success('!')
      } else if (this.activeIndex === '2') {
        // ,
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: {
              sel: 'only'
            }
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('!')
        }
        this.onlyTableData = res.data
        console.log(res.data)
      }
    },
    // 
    picUploaded (res) {
      const pic = { pic: res.data.tmp_path }

      this.addForm.pics.push(pic)
      // console.log(this.addForm.pics)
    },
    // 
    handlePreview (file) {
      console.log(file)
      const url = file.response.data.url
      this.previewPath = url
      this.previewDialogVisible = true
    },
    // 
    handleRemove (picInfo) {
      // 
      const pic = picInfo.response.data.tmp_path
      // 
      const i = this.addForm.pics.findIndex((x) => x.pic === pic)
      // 
      this.addForm.pics.splice(i, 1)
      // console.log(this.addForm.pics)
    },
    // 
    addGoods () {
      // console.log(this.addForm)
      // 
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return this.$message.error('')
        }
        // 
        // addForm
        const form = _.cloneDeep(this.addForm)
        // api
        form.goods_cat = form.goods_cat.join(',')
        this.manyTableData.forEach(item => {
          const newObj = { attr_id: item.attr_id, attr_value: item.attr_vals.join(',') }
          form.attrs.push(newObj)
        })
        // console.log(this.manyTableData)
        this.onlyTableData.forEach(item => {
          const newObj = { attr_id: item.attr_id, attr_vals: item.attr_vals }
          form.attrs.push(newObj)
        })
        // console.log(this.onlyTableData)
        // console.log(form)
        const { data: res } = await this.$http.post('goods', form)
        if (res.meta.status !== 201) {
          return this.$message.error('!')
        }
        this.$message.success('!')
        this.$router.push('/goods')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.el-steps {
  margin-top: 15px;
  margin-bottom: 15px;
}
.el-checkbox {
  margin: 0 10px 0 0 !important;
}

.btnAdd{
    margin-top: 20px;
}
</style>
