<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"
            ></el-button
          >
        </el-col>
      </el-row>

      <!--  -->
      <tree-table
        :data="goodsList"
        :columns="colunms"
        border
        :selection-type="false"
        :expand-type="false"
        :show-index="true"
        index-text="#"
        :show-row-hover="true"
      >
        <!--  -->
        <template v-slot:cat_deleted="props">
          <!-- <pre>
                {{props.row}}
            </pre> -->
          <i
            v-if="!props.row.cat_deleted"
            class="el-icon-success"
            style="color: lightgreen"
          ></i>
          <i
            v-if="props.row.cat_deleted"
            style="color: lightgreen"
            class="el-icon-error"
          ></i>
        </template>
        <!--  -->
        <template v-slot:cat_level="props">
          <!-- <pre>
                {{props.row}}
            </pre> -->
          <el-tag v-if="props.row.cat_level === 0" type="primary"></el-tag>
          <el-tag v-if="props.row.cat_level === 1" type="success"></el-tag>
          <el-tag v-if="props.row.cat_level === 2" type="warning"></el-tag>
        </template>
        <!--  -->
        <template v-slot:operation>
          <!-- <pre>
                {{props.row}}
            </pre> -->
          <el-button type="primary" icon="el-icon-edit" size="mini"
            ></el-button
          >
          <el-button type="danger" icon="el-icon-setting" size="mini"
            ></el-button
          >
        </template>
      </tree-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addCateDialogVisible"
        width="50%"
        @close="addCateDialogClosed"
      >
        <!--  -->
        <el-form
          :model="addCateForm"
          :rules="addCateFormRules"
          ref="addCateFormRef"
          label-width="100px"
        >
          <el-form-item label="" prop="cat_name">
            <el-input v-model="addCateForm.cat_name"></el-input>
          </el-form-item>
          <!-- options  -->
          <!-- props  -->
          <el-form-item label="">
            <el-cascader
              v-model="selectedKeys"
              :options="parentCateList"
              @change="parentCateChanged"
              clearable
              :props="cascaderProps"
            ></el-cascader>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="addCateDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addCate"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      goodsList: [],
      // 
      queryInfo: {
        type: 3,
        pagesize: 5,
        pagenum: 1
      },
      // 
      total: 0,
      // tree-table
      colunms: [
        {
          label: '',
          prop: 'cat_name',
          width: '200px'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'cat_deleted'
        },
        {
          label: '',
          type: 'template',
          template: 'cat_level'
        },
        {
          label: '',
          type: 'template',
          template: 'operation',
          width: '200px'
        }
      ],
      // 
      addCateDialogVisible: false,
      // 
      addCateForm: {
        // id
        cat_pid: 0,
        // 
        cat_name: '',
        // ,1
        cat_level: 0
      },
      // 
      addCateFormRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      parentCateList: [],
      // 
      cascaderProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        checkStrictly: true
      },
      // id
      selectedKeys: []
    }
  },
  created () {
    // 
    this.getGoodsList()
  },
  methods: {
    // 
    async getGoodsList () {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.goodsList = res.data.result
      this.total = res.data.total
      //   console.log(res);
    },
    //  pagesize 
    handleSizeChange (pagesize) {
      this.queryInfo.pagesize = pagesize
      this.getGoodsList()
    },
    //   
    handleCurrentChange (pagenum) {
      this.queryInfo.pagenum = pagenum
      this.getGoodsList()
    },
    // 
    async showAddCateDialog () {
      // 
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.parentCateList = res.data
      //   console.log(res)
      // 
      this.addCateDialogVisible = true
      this.$message.success('')
    },
    // 
    parentCateChanged () {
      // selectedKeysid
      switch (this.selectedKeys.length) {
        // 1,
        case 0:
          this.addCateForm.cat_pid = 0
          this.addCateForm.cat_level = 0
          break
          // 2
        case 1:
          this.addCateForm.cat_pid = this.selectedKeys[0]
          this.addCateForm.cat_level = 1
          break
          // 3
        case 2:
          this.addCateForm.cat_pid = this.selectedKeys[1]
          this.addCateForm.cat_level = 2
          break
      }
      //  console.dir(this.addCateForm)
    },
    // 
    addCate () {
    //   
      this.$refs.addCateFormRef.validate(async (valid) => {
        if (!valid) {
          return
        }
        // ,
        const { data: res } = await this.$http.post('categories', this.addCateForm)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        // 
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_name = ''
        this.addCateForm.cat_level = 0
        this.selectedKeys = []
        // 
        this.addCateDialogVisible = false
        // 
        this.getGoodsList()
      })
    //   console.log(this.addCateForm);
    },
    // ,
    addCateDialogClosed () {
      this.$refs.addCateFormRef.resetFields()
      this.selectedKeys = []
      this.addCateForm.cat_level = 0
      this.addCateForm.cat_pid = 0
    }
  }
}
</script>

<style lang="less" scoped>
.zk-table {
  margin-top: 15px;
}
.el-cascader{
    width: 100%;
}

</style>
