<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            class="input-search"
            clearable
            @clear="getGoodsList"
          >
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
          </el-input>
          <el-button type="primary" @click="goAddPage"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table class="goods-table" :data="goodsList" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column
          prop="goods_name"
          label=""
          width="300px"
        ></el-table-column>
        <el-table-column
          prop="goods_price"
          label="()"
          width="80px"
        ></el-table-column>
        <el-table-column
          prop="goods_weight"
          label=""
          width="80px"
        ></el-table-column>
        <el-table-column prop="add_time" label="" width="150px">
          <template v-slot="props">
            {{props.row.add_time|formatDate}}
          </template>
        </el-table-column>
        <el-table-column label="" width="130px">
          <template v-slot="props">
            <el-button
              class="el-icon-edit"
              type="primary"
              size="mini"
            ></el-button>
            <el-button
              class="el-icon-delete"
              type="danger"
              size="mini"
              @click="deleteGood(props.row.goods_id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 20, 30]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        :background="true"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      goodsList: [],
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      // 
      total: 0
    }
  },
  created () {
    this.getGoodsList()
  },
  methods: {
    // 
    async getGoodsList () {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.goodsList = res.data.goods
      this.total = res.data.total
    },
    //  pagesize 
    handleSizeChange (newPagesize) {
      this.queryInfo.pagesize = newPagesize
      this.getGoodsList()
    },
    //  pagenum 
    handleCurrentChange (newPagenum) {
      this.queryInfo.pagenum = newPagenum
      this.getGoodsList()
    },
    // 
    async deleteGood (goodsId) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete('goods/' + goodsId)
      if (res.meta.status !== 200) {
        return this.$$message.error('')
      }
      this.getGoodsList()
      return this.$message.success('')
    },
    // 
    goAddPage () {
      this.$router.push('/goods/add')
    }
  }
}
</script>

<style lang="less" scoped>
.input-search {
  width: 350px;
  margin-right: 15px;
}
</style>
